import React, { useMemo, useState } from "react";
import { ServerBusiness } from "../types/Business";
import {
  makeStyles,
  Box,
  Card,
  CardContent,
  BoxProps,
  Select,
  MenuItem,
  InputLabel,
  FormControl,
  useTheme,
  useMediaQuery,
} from "@material-ui/core";
import BusinessView, { RatingProp } from "./BusinessView";
import { DateTime } from "luxon";
import { getStringSortKey } from "../utils/String";

const useStyles = makeStyles((theme) => ({
  searchContainer: {
    marginTop: `-${theme.spacing(4)}px`,
  },
  fieldsContainer: {
    display: "grid",
    gridGap: theme.spacing(1),
    gridTemplateColumns: "repeat(2, 1fr)",
    [theme.breakpoints.down("xs")]: {
      gridTemplateColumns: "1fr",
    },
  },
  freeTextField: {
    gridRow: 1,
    gridColumn: "1 / 3",
    [theme.breakpoints.down("xs")]: {
      gridColumn: 1,
    },
  },
  resultsContainer: {
    display: "grid",
    gridGap: theme.spacing(1),
    gridTemplateColumns: "repeat(3, 1fr)",
    alignItems: "start",
    [theme.breakpoints.only("xs")]: {
      gridTemplateColumns: "repeat(2, 1fr)",
    },
    [theme.breakpoints.down("xs")]: {
      gridTemplateColumns: "1fr",
    },
  },
}));

interface BusinessesContainerProps {
  businesses: ServerBusiness[];
  buttons: (business: ServerBusiness) => JSX.Element;
  containerProps?: BoxProps;
  disableElevation?: boolean;
  rating?: RatingProp;
}

enum SortMethod {
  lastUpdate,
  averageRating,
}

const getBusinessSortingKey = (
  sortMethod: SortMethod,
  business: ServerBusiness
) => {
  switch (sortMethod) {
    case SortMethod.averageRating:
      return business.averageRating || 2.5; // we treat no rating as neutral rating
    case SortMethod.lastUpdate:
      return DateTime.max(
        business.updatedAt,
        ...(business.reviews || []).map((review) => review.createdAt)
      ).toMillis();
    default:
      throw Error(`Unknown SortMethod ${sortMethod}`);
  }
};

const BusinessesContainer = ({
  businesses,
  buttons,
  containerProps,
  disableElevation,
  rating,
}: BusinessesContainerProps) => {
  const classes = useStyles();
  const theme = useTheme();
  const isSmallScreen = useMediaQuery(theme.breakpoints.down("xs"));

  const [sortMethod, setSortMethod] = useState(SortMethod.averageRating);

  const displaySortControl = isSmallScreen
    ? businesses.length >= 2
    : businesses.length >= 3;

  const sortedBusinesses = useMemo(
    () =>
      businesses.sort(
        (businessA, businessB) =>
          getBusinessSortingKey(sortMethod, businessB) -
            getBusinessSortingKey(sortMethod, businessA) || // high -> low using key
          getStringSortKey(businessA.name.trim(), businessB.name.trim()) // a -> z by string comparing key
      ),
    [businesses, sortMethod]
  );

  return (
    <>
      {displaySortControl && (
        <Box mt={3} display="flex" justifyContent="start">
          <FormControl variant="filled" size="small" fullWidth={isSmallScreen}>
            <InputLabel> </InputLabel>
            <Select
              value={sortMethod}
              onChange={(e) => setSortMethod(e.target.value as SortMethod)}
            >
              <MenuItem value={SortMethod.averageRating}> </MenuItem>
              <MenuItem value={SortMethod.lastUpdate}> </MenuItem>
            </Select>
          </FormControl>
        </Box>
      )}

      <Box
        className={classes.resultsContainer}
        mt={2}
        mb={2}
        {...containerProps}
      >
        {sortedBusinesses.map((business) => (
          <Card
            key={business.id}
            elevation={disableElevation ? 0 : 1}
            style={{ height: "100%" }}
          >
            <CardContent
              style={{
                padding: 0,
                height: "100%",
                display: "flex",
                flexDirection: "column",
              }}
            >
              <BusinessView
                business={business}
                buttons={buttons(business)}
                fullWidthButtons
                rating={rating}
              />
            </CardContent>
          </Card>
        ))}
      </Box>
    </>
  );
};

export default BusinessesContainer;
