import React, { useEffect, useState } from "react";
import {
  Box,
  Button,
  Dialog,
  DialogContent,
  DialogContentText,
  Typography,
} from "@material-ui/core";

const pushState = () => {
  window.history.pushState({ withinAskBeforeExit: true }, "", "");
};

export const askBeforeExit = <Props extends {}>(
  Component: React.ComponentType<Props>
): React.FC<Props> => (props: Props) => {
  const [isDialogOpen, setIsDialogOpen] = useState(false);

  useEffect(pushState, []);
  useEffect(() => {
    if (isDialogOpen) {
      return;
    }

    const handlePopState = () => {
      setIsDialogOpen(true);
    };
    window.addEventListener("popstate", handlePopState);
    return () => window.removeEventListener("popstate", handlePopState);
  }, [isDialogOpen]);

  const onChooseStay = () => {
    pushState();
    setIsDialogOpen(false);
  };
  const onChooseExit = () => {
    window.history.back();
  };

  return (
    <>
      <Component {...(props as Props)} />
      <Dialog
        onClose={() => setIsDialogOpen(false)}
        open={isDialogOpen}
        onBackdropClick={onChooseStay}
      >
        <DialogContent>
          <Typography>  ?</Typography>
          <DialogContentText id="alert-dialog-description">
                     . .
            <br />
                     (   
               !),   .:
            <br />
              ?
          </DialogContentText>
        </DialogContent>
        <Box display="flex" justifyContent="space-between" px={3} pb={2}>
          <Button onClick={onChooseStay} variant="contained" color="primary">
            , !
          </Button>
          <Button onClick={onChooseExit} variant="outlined" autoFocus>
             ,  
          </Button>
        </Box>
      </Dialog>
    </>
  );
};
