import React from "react";
import { withStyles } from "@material-ui/core";
import { Rating as MuiRating, RatingProps } from "@material-ui/lab";
import { StarBorder, StarTwoTone } from "@material-ui/icons";

const StylesMuiRating = withStyles({
  iconFilled: {
    color: "#8e24aa",
  },
  iconHover: {
    color: "#a739c6",
  },
})(MuiRating);

const Rating = (props: RatingProps) => (
  <StylesMuiRating
    precision={props.readOnly ? 0.5 : 1}
    emptyIcon={<StarBorder fontSize="inherit" />}
    icon={<StarTwoTone fontSize="inherit" />}
    color="secondary"
    {...props}
  />
);

export default Rating;
