import React from "react";
import {
  CircularProgress,
  makeStyles,
  CircularProgressProps,
} from "@material-ui/core";

const useStyles = makeStyles({
  "@keyframes MultiColorProgressSpinner": {
    "0%": {
      color: "rgb(245,172,202)",
    },
    "33.29%": {
      color: "rgb(245,172,202)",
    },
    "33.31%": {
      color: "rgb(255,255,255)",
    },
    "66.59%": {
      color: "rgb(255,255,255)",
    },
    "66.61%": {
      color: "rgb(107,175,225)",
    },
    "100%": {
      color: "rgb(107,175,225)",
    },
  },
  circularProgress: {
    "& .MuiCircularProgress-svg": {
      animation: "$MultiColorProgressSpinner 4.2s ease-in-out infinite", // 1.4s per spin * 3 spins
    },
  },
});

const MultiColorProgressSpinner = (props: CircularProgressProps) => {
  const classes = useStyles();
  return (
    <CircularProgress
      {...props}
      thickness={props.thickness || 5}
      className={classes.circularProgress}
    />
  );
};

export default MultiColorProgressSpinner;
