import React from "react";
import {
  makeStyles,
  Backdrop,
  Typography,
  Box,
  useTheme,
  useMediaQuery,
} from "@material-ui/core";
import MultiColorProgressSpinner from "./MultiColorProgressSpinner";

const useStyles = makeStyles((theme) => ({
  backdrop: {
    zIndex: theme.zIndex.drawer + 1,
    "@supports (backdrop-filter: blur(0.5rem) brightness(25%))": {
      backdropFilter: "blur(0.5rem) brightness(25%)",
      backgroundColor: "rgba(0,0,0,0)",
    },
  },
  loadingTitle: {
    color: "white",
    fontWeight: "bold",
    fontSize: "1.5rem",
    marginLeft: theme.spacing(1),
    [theme.breakpoints.down("xs")]: {
      fontSize: "1rem",
      marginLeft: 0,
      marginTop: theme.spacing(1.5),
    },
  },
}));

interface LoadingBackdropProps {
  open: boolean;
  text?: string;
}

const LoadingBackdrop = ({ open, text }: LoadingBackdropProps) => {
  const classes = useStyles();
  const theme = useTheme();
  const isSmallScreen = useMediaQuery(theme.breakpoints.down("xs"));

  return (
    <Backdrop className={classes.backdrop} open={open}>
      <Box
        display="flex"
        alignItems="center"
        flexDirection={isSmallScreen ? "column" : "row"}
      >
        <MultiColorProgressSpinner
          size={text ? "1.5rem" : isSmallScreen ? "7vw" : "2rem"}
          thickness={text ? 6 : 5}
        />
        {text && (
          <Typography className={classes.loadingTitle}>{text}</Typography>
        )}
      </Box>
    </Backdrop>
  );
};

export default LoadingBackdrop;
