import { DateTime } from "luxon";

export const formatDateToDisplay = (date: Date) => {
  return date.toLocaleDateString("he", {
    day: "numeric",
    month: "numeric",
    year: "2-digit",
  });
};

export function timeSinceInHebrew(date: DateTime): string {
  let diff = DateTime.local().diff(date);

  const difference = {
    years: Math.floor(diff.as("years")),
    months: Math.floor(diff.as("months")),
    days: Math.floor(diff.as("days")),
    hours: Math.floor(diff.as("hours")),
    minutes: Math.floor(diff.as("minutes")),
    seconds: Math.floor(diff.as("seconds")),
  };

  if (difference.years > 1) {
    return ` ${difference.years} `;
  }
  if (difference.months > 1) {
    return ` ${difference.months} `;
  }
  if (difference.days > 1) {
    return ` ${difference.days} `;
  }
  if (difference.hours > 1) {
    return ` ${difference.hours} `;
  }
  if (difference.minutes > 1) {
    return ` ${difference.minutes} `;
  }
  if (difference.seconds > 1) {
    return ` ${difference.seconds} `;
  }

  return " ";
}
