import React, { Fragment } from "react";
import { Typography, makeStyles, Box, Badge, Tooltip } from "@material-ui/core";
import {
  PlaceTwoTone,
  LabelTwoTone,
  CallTwoTone,
  TranslateTwoTone,
  SpaTwoTone,
  BookTwoTone,
  MapTwoTone,
  FiberManualRecordTwoTone,
} from "@material-ui/icons";
import Rating from "./Rating";
import Business, { ServerBusiness } from "../types/Business";
import { formatForDisplaying } from "../utils/Array";

const useStyles = makeStyles((theme) => ({
  container: {
    fontSize: theme.typography.body1.fontSize,
  },
  name: {
    flexGrow: 1,
    marginRight: theme.spacing(2),
    lineHeight: "1em",
  },
  ratingStars: {
    fontSize: "1.1em",
  },
  ratingNumber: {
    fontSize: "1.1rem",
    lineHeight: "1em",
    "& > .MuiBadge-badge": {
      transform: "unset",
      position: "unset",
    },
  },
  tinyText: {
    fontSize: ".8em",
  },
  details: {
    display: "grid",
    gridTemplateColumns: "1.1em 1fr",
    gridGap: ".25em .5em",
    "& .MuiSvgIcon-root": {
      fontSize: "1.1em",
      height: "1.1em",
      width: "1.1em",
    },
  },
  buttonsContainer: {
    display: "flex",
  },
  buttonsContainerSpaceBetweenButtons: {
    justifyContent: "center",
  },
  buttonsContainerFullWidthButtons: {
    [theme.breakpoints.up("xs")]: {
      borderTop: "1px solid #f7ecf9",
      "& .MuiButton-root": {
        borderRadius: 0,
        flexGrow: 1,
      },
    },
  },
}));

export type RatingProp = "number" | "stars";

type BusinessViewProps = {
  buttons?: JSX.Element;
  fullWidthButtons?: boolean;
  detailed?: boolean;
} & (
  | {
      business: Business;
      rating: false;
    }
  | {
      business: ServerBusiness;
      rating?: RatingProp;
    }
);

type BusinessAttributeIconAndValuesProps = {
  icon: JSX.Element;
  title: string;
  content: JSX.Element | string | string[];
};
const BusinessAttributeIconAndValues = ({
  icon,
  title,
  content,
}: BusinessAttributeIconAndValuesProps) => (
  <>
    <Tooltip title={title} aria-label={title}>
      {icon}
    </Tooltip>
    <Tooltip title={title} aria-label={title} placement="bottom-start">
      <Box>
        {Array.isArray(content) ? (
          <>
            {content.map((item, index) => (
              <Fragment key={index}>
                {0 < index && <br />}
                {item}
              </Fragment>
            ))}
          </>
        ) : (
          content
        )}
      </Box>
    </Tooltip>
  </>
);

const BusinessView = ({
  business,
  buttons,
  fullWidthButtons = false,
  detailed = false,
  rating,
}: BusinessViewProps) => {
  const classes = useStyles();

  const ratingValue =
    rating !== null ? (business as ServerBusiness).averageRating : null;

  return (
    <Fragment>
      <Box className={classes.container} p={2} pb={1} style={{ flexGrow: 1 }}>
        <Box display="flex" alignItems="center" flexWrap="wrap" mb={1}>
          <Typography variant="h6" className={classes.name}>
            {business.name}
          </Typography>
          {ratingValue !== null && (
            <>
              {rating === "stars" && (
                <Rating
                  className={classes.ratingStars}
                  value={ratingValue}
                  readOnly
                />
              )}
              {rating === "number" && ratingValue && (
                <Badge
                  badgeContent={ratingValue}
                  color="secondary"
                  className={classes.ratingNumber}
                />
              )}
            </>
          )}
        </Box>

        <Box className={classes.details}>
          <BusinessAttributeIconAndValues
            title=""
            icon={<LabelTwoTone />}
            content={formatForDisplaying(business.info.profession)}
          />

          {detailed && (
            <BusinessAttributeIconAndValues
              title=""
              icon={<BookTwoTone />}
              content={formatForDisplaying(business.info.expertise)}
            />
          )}

          <BusinessAttributeIconAndValues
            title=""
            icon={<MapTwoTone />}
            content={formatForDisplaying(business.info.area)}
          />

          {business.info.locations && (
            <BusinessAttributeIconAndValues
              title=""
              icon={<PlaceTwoTone />}
              content={business.info.locations.map(
                (location) => `${location.city.name}, ${location.addressInCity}`
              )}
            />
          )}

          {detailed && (
            <BusinessAttributeIconAndValues
              title=" "
              icon={<CallTwoTone />}
              content={business.info.phoneNumbers}
            />
          )}

          <BusinessAttributeIconAndValues
            title=""
            icon={<TranslateTwoTone />}
            content={formatForDisplaying(business.info.language)}
          />

          <BusinessAttributeIconAndValues
            title=" "
            icon={<SpaTwoTone />}
            content={formatForDisplaying(business.info.healthCare)}
          />

          <BusinessAttributeIconAndValues
            title=""
            icon={<FiberManualRecordTwoTone />}
            content={formatForDisplaying(business.info.gender)}
          />
        </Box>
      </Box>
      {buttons && (
        <Box
          className={
            classes.buttonsContainer +
            ` ${
              fullWidthButtons
                ? classes.buttonsContainerFullWidthButtons
                : classes.buttonsContainerSpaceBetweenButtons
            }`
          }
        >
          {buttons}
        </Box>
      )}
    </Fragment>
  );
};

export default BusinessView;
