import React, { Fragment, useState } from "react";
import { makeStyles, Theme, createStyles } from "@material-ui/core/styles";
import {
  Box,
  Card,
  CardContent,
  Typography,
  Stepper,
  Step,
  StepLabel,
} from "@material-ui/core";
import Step1, { Step1ResultType } from "./Step1";
import Step3 from "./Step3";
import Step4 from "./Step4";
import { ServerBusiness } from "../../types/Business";
import { requireAuthenticated } from "../../hocs/requiredAuthenticated";
import { Proposal, BusinessProposal } from "../../types/Proposal";
import Step2 from "./Step2";
import server from "../../server";
import LoadingBackdrop from "../../components/LoadingBackdrop";
import CountdownButton from "../../components/CountdownButton";
import { askBeforeExit } from "../../hocs/askBeforeExit";
import { DateTime } from "luxon";

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    root: {
      width: "100%",
    },
    button: {
      marginTop: theme.spacing(1),
      marginRight: theme.spacing(1),
    },
    actionsContainer: {
      marginBottom: theme.spacing(2),
    },
    resetContainer: {
      padding: theme.spacing(3),
    },
  })
);

function getSteps() {
  return [" . ", " ", "", " "];
}

const steps = getSteps();

type ProposeScreenProps = {
  selectedBusiness: ServerBusiness | null;
};

const ProposeScreen = ({ selectedBusiness }: ProposeScreenProps) => {
  const classes = useStyles();
  const [activeStep, setActiveStep] = React.useState(selectedBusiness ? 1 : 0);

  const handleNext = () => {
    setActiveStep((prevActiveStep) => prevActiveStep + 1);
  };

  const handleBack = () => {
    setActiveStep((prevActiveStep) => prevActiveStep - 1);
  };

  // Step 1 result
  const [businessName, setBusinessName] = useState<string | null>(
    selectedBusiness ? selectedBusiness.name : null
  );
  const [
    existingBusiness,
    setExistingBusiness,
  ] = useState<ServerBusiness | null>(selectedBusiness);

  // Step 2 result
  const [
    businessResult,
    setBusinessProposal,
  ] = useState<BusinessProposal | null>(null);

  // Step 3 result
  const [rating, setRating] = useState<number | null>(null);
  const [moreDetails, setMoreDetails] = useState<string>("");

  const [sent, setSent] = useState(false);
  const [sentSuccessfully, setSentSuccessfully] = useState(false);
  const [errorInfo, setErrorInfo] = useState<{
    proposal: Proposal;
    timestamp: DateTime;
  } | null>(null);

  const [isLoadingBackdropOpen, setIsLoadingBackdropOpen] = useState(false);
  const send = (proposal: Proposal) => {
    const showBackdropTimeout = setTimeout(() => {
      setIsLoadingBackdropOpen(true);
    }, 500);

    server
      .propose(proposal)
      .then(() => {
        setSentSuccessfully(true);
        setSent(true);
      })
      .catch(() => {
        setErrorInfo({ proposal, timestamp: DateTime.local() });
        setSentSuccessfully(false);
        setSent(true);
      })
      .finally(() => {
        setActiveStep(4);
        clearTimeout(showBackdropTimeout);
        setIsLoadingBackdropOpen(false);
      });
  };

  return (
    <Fragment>
      <Card style={{ overflow: "visible" }}>
        <CardContent style={{ padding: 0 }}>
          <Box p={3}>
            <div className={classes.root}>
              {activeStep < steps.length && (
                <Box mb={2}>
                  <Typography variant="h5" color="textPrimary">
                      - {steps[activeStep]}
                  </Typography>
                </Box>
              )}
              <Box>
                {activeStep === 0 && (
                  <Step1
                    onResult={(result) => {
                      switch (result.type) {
                        case Step1ResultType.createNew:
                          setBusinessName(result.businessName);
                          return handleNext();
                        case Step1ResultType.useExisting:
                          setExistingBusiness(result.business);
                          return handleNext();
                      }
                    }}
                  />
                )}
                {activeStep === 1 && (
                  <Step2
                    name={businessName}
                    existingBusiness={existingBusiness}
                    handleBack={handleBack}
                    onResult={(result) => {
                      setBusinessProposal(result);
                      handleNext();
                    }}
                  />
                )}
                {activeStep === 2 && (
                  <Step3
                    rating={rating}
                    setRating={setRating}
                    moreDetails={moreDetails}
                    setMoreDetails={setMoreDetails}
                    handleBack={handleBack}
                    handleNext={handleNext}
                  />
                )}
                {activeStep === 3 &&
                  businessResult !== null &&
                  rating !== null && (
                    <Step4
                      businessResult={businessResult}
                      rating={rating}
                      moreDetails={moreDetails}
                      handleBack={handleBack}
                      handleNext={handleNext}
                      onResult={(proposal) => {
                        send(proposal);
                      }}
                    />
                  )}
                {sent &&
                  (sentSuccessfully ? (
                    <Box mb={2}>
                      <Typography variant="h5" color="textPrimary" gutterBottom>
                           !
                      </Typography>
                      <Typography>
                                .&nbsp;
                        <span role="img" aria-label=" ">
                          
                        </span>
                      </Typography>
                    </Box>
                  ) : (
                    <>
                      <Box mb={2}>
                        <Typography
                          variant="h5"
                          color="textPrimary"
                          gutterBottom
                        >
                           {" "}
                          <span role="img" aria-label=" ">
                            
                          </span>
                        </Typography>
                        <Typography>
                                .
                          <br />
                                  .
                          <br />
                             , .  . 
                            .
                        </Typography>
                      </Box>
                      {errorInfo && (
                        <Box mb={1} display="flex" justifyContent="center">
                          <CountdownButton
                            variant="contained"
                            color="primary"
                            onClick={() => send(errorInfo.proposal)}
                            enablingTime={errorInfo.timestamp.plus({
                              seconds: 30,
                            })}
                          >
                             
                          </CountdownButton>
                        </Box>
                      )}
                    </>
                  ))}
              </Box>
              <Stepper activeStep={activeStep} alternativeLabel>
                {steps.map((label) => (
                  <Step key={label}>
                    <StepLabel>{label}</StepLabel>
                  </Step>
                ))}
              </Stepper>
            </div>
          </Box>
        </CardContent>
      </Card>
      <LoadingBackdrop open={isLoadingBackdropOpen} />
    </Fragment>
  );
};

export default requireAuthenticated(askBeforeExit(ProposeScreen));
